.chat{
    flex:0.65;
    display: flex;
    flex-direction: column;
}

.chat__header{
    display: flex;
    padding:20px;
    align-items: center;
    border-bottom:1px solid lightgray;
}

.chat__headerInfo{
    flex:1;
    padding-left:20px;
}

.chat__headerInfo > h3{
    margin-bottom: 3px;
    font-weight: 500;
}

.chat__headerInfo > p{
    color:gray;
}

.chat__headerRight{
    display:flex;
    justify-content:space-between;
    min-width: 100px;
}

.chat__body{
    flex:1;
    background-image: url("data:image/jpeg;base64,");
    background-repeat: repeat;
    background-position: center;
    padding:30px;
    overflow:scroll;
}

.chat__message{
    position: relative;
    font-size:16px;
    padding:10px;
    background-color:white;
    border-radius: 10px;
    width:fit-content;
    margin-bottom: 20px;
}
.chat__receiver{
    margin-left:auto;
    background-color:#dcf8c6;
}

.chat__name{
    position:absolute;
    top: -15px;
    font-size:12px;
    font-weight: bold;
    color:white;
}

.chat__timestamp{
    margin-left: 10px;
    font-size:xx-small;
}

.chat__footer{
    display:flex;
    justify-content:space-between;
    align-items:center;
    height:62px;
    border-top: 1px solid lightgray;
}

.chat__footer > form{
    flex:1;
    display:flex;
}

.chat__footer >form> input{
    flex:1;
    border-radius:30px;
    padding:10px;
    border:none;
}

.chat__footer > .MuiSvgIcon-root{
    color:gray;
    padding:10px;
}

.chat__footer >form> button{
    display:none;
}
